'use strict';

// MODULES //

import Complex128 from '@stdlib/complex/float64/ctor';
import copysign from './../../../../base/special/copysign';
import sincos from './../../../../base/special/sincos';
import isnan from './../../../../base/assert/is-nan';
import isInfinite from './../../../../base/assert/is-infinite';
import exp from './../../../../base/special/exp';
import PINF from '@stdlib/constants/float64/pinf';
import NINF from '@stdlib/constants/float64/ninf';
import real from '@stdlib/complex/float64/real';
import imag from '@stdlib/complex/float64/imag';


// MAIN //

/**
* Evaluates the exponential function for a double-precision complex floating-point number.
*
* @param {Complex128} z - complex number
* @returns {Complex128} result
*
* @example
* import Complex128 from '@stdlib/complex/float64/ctor';
* import real from '@stdlib/complex/float64/real';
* import imag from '@stdlib/complex/float64/imag';
* import cexp from '@stdlib/math/base/special/cexp';
*
* var v = cexp( new Complex128( 0.0, 0.0 ) );
* // returns <Complex128>
*
* var re = real( v );
* // returns 1.0
*
* var im = imag( v );
* // returns 0.0
*
* @example
* import Complex128 from '@stdlib/complex/float64/ctor';
* import real from '@stdlib/complex/float64/real';
* import imag from '@stdlib/complex/float64/imag';
* import cexp from '@stdlib/math/base/special/cexp';
*
* var v = cexp( new Complex128( 1.0, 0.0 ) );
* // returns <Complex128>
*
* var re = real( v );
* // returns ~2.718
*
* var im = imag( v );
* // returns 0.0
*/
function cexp( z ) {
	var out;
	var im;
	var re;
	var e;

	re = real( z );
	im = imag( z );

	if ( isnan( re ) ) {
		re = NaN;
		im = ( im === 0.0 ) ? im : re;
	} else if ( isInfinite( im ) ) {
		if ( re === PINF ) {
			re = -re;
			im = NaN;
		} else if ( re === NINF ) {
			re = -0.0;
			im = copysign( 0.0, im );
		} else {
			re = NaN;
			im = NaN;
		}
	} else {
		e = exp( re );
		if ( im === 0.0 ) {
			re = e;
		} else {
			out = sincos( im );
			re = out[ 1 ] * e;
			im = out[ 0 ] * e;
		}
	}
	return new Complex128( re, im );
}


// EXPORTS //

export default cexp;
