'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

/*
* The following modules are intentionally not exported: napi
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name assert
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base/assert}
*/
import assert from './../../base/assert';
setReadOnly( ns, 'assert', assert );

/**
* @name ops
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base/ops}
*/
import ops from './../../base/ops';
setReadOnly( ns, 'ops', ops );

/**
* @name special
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base/special}
*/
import special from './../../base/special';
setReadOnly( ns, 'special', special );

/**
* @name tools
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base/tools}
*/
import tools from './../../base/tools';
setReadOnly( ns, 'tools', tools );

/**
* @name utils
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base/utils}
*/
import utils from './../../base/utils';
setReadOnly( ns, 'utils', utils );


// EXPORTS //

export default ns;
