'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name absdiff
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/utils/absolute-difference}
*/
import absdiff from './../../../base/utils/absolute-difference';
setReadOnly( ns, 'absdiff', absdiff );

/**
* @name epsdiff
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/utils/float64-epsilon-difference}
*/
import epsdiff from './../../../base/utils/float64-epsilon-difference';
setReadOnly( ns, 'epsdiff', epsdiff );

/**
* @name reldiff
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/utils/relative-difference}
*/
import reldiff from './../../../base/utils/relative-difference';
setReadOnly( ns, 'reldiff', reldiff );


// EXPORTS //

export default ns;
