'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name base
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/base}
*/
import base from './../base';
setReadOnly( ns, 'base', base );

/**
* @name iter
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/iter}
*/
import iter from './../iter';
setReadOnly( ns, 'iter', iter );

/**
* @name special
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/special}
*/
import special from './../special';
setReadOnly( ns, 'special', special );

/**
* @name strided
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/strided}
*/
import strided from './../strided';
setReadOnly( ns, 'strided', strided );

/**
* @name tools
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/math/tools}
*/
import tools from './../tools';
setReadOnly( ns, 'tools', tools );


// EXPORTS //

export default ns;
