'use strict';

// MODULES //

import iterMap from './../../../../iter/tools/map';
import gamma1pm1 from './../../../../base/special/gamma1pm1';


// MAIN //

/**
* Returns an iterator which iteratively computes `gamma(x+1) - 1` without cancellation errors for small `x`.
*
* ## Notes
*
* -   If an environment supports `Symbol.iterator` **and** a provided iterator is iterable, the returned iterator is iterable.
*
* @param {Iterator} iterator - input iterator
* @throws {TypeError} must provide an iterator protocol-compliant object
* @returns {Iterator} iterator
*
* @example
* import uniform from '@stdlib/random/iter/uniform';
*
* var iter = iterGamma1pm1( uniform( -5.0, 5.0 ) );
*
* var v = iter.next().value;
* // returns <number>
*
* v = iter.next().value;
* // returns <number>
*
* v = iter.next().value;
* // returns <number>
*
* // ...
*/
function iterGamma1pm1( iterator ) {
	return iterMap( iterator, gamma1pm1 );
}


// EXPORTS //

export default iterGamma1pm1;
